.DataVisualizer table {
  width: 100%;
  border-collapse: collapse;
  -webkit-box-shadow: 3px 3px 3px #9B9B9B;
          box-shadow: 3px 3px 3px #9B9B9B;
  border: 2px solid #E2E1E1;
}

.DataVisualizer td {
  border: 1px solid #E2E1E1;
  padding: 6px;
}

@media screen and (min-width: 768px) {
  .DataVisualizer {
    margin: 0 20%;
  }
}


